<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default ({
  inject: ['reload'],
  data () {
    return {
      columns: [
          {
            title: 'ID',
            key: 'id',
            width: 80, 
            align: 'center'
          },
          {
            title: '名称',
            key: 'game_name',
            width: 200, 
            align: 'center'
          },
          {
            title: 'id',
            key: 'game_id',
            width: 60, 
            align: 'center'
          },
          {
            title: '手机号码/QQ号码',
            key: 'phone',
            width: 140, 
            align: 'center'
          },
          {
            title: 'ip',
            key: 'ip',
            width: 100, 
            align: 'center'
          },
          {
            title: '系统类型',
            key: 'system',
            width: 80, 
            align: 'center'
          },
          {
            title: '反馈理由',
            key: 'reason_name',
            width: 340, 
            align: 'center',
            render: (h,params) => {
              if(params.row) {
                const reason_name = params.row.listreason_name;
                return h('div', {}, reason_name)
              }
            }
          },
          {
            title: '反馈备注',
            key: 'remake',
            width: 200, 
            align: 'center'
          },
          {
            title: '反馈时间',
            key: 'addtime',
            width: 140, 
            align: 'center'
          },
          {
            title: '预览',
            key: 'pcmurl',
            width: '', 
            align: 'center',
            render: (h,params) => {
              const m_url = params.row.m_url;
              const pc_url = params.row.pc_url;
              return h('div', [
                h('Button', {
                  props: {
                    type: 'warning',
                    size: 'small'
                  },
                    style: {
                    margin: '0 4px 2px 0'
                  },
                    on: {
                      click: () => {
                        window.open(pc_url,'_blank')
                      }
                  }
                }, 'pc预览'),
                h('Button', {
                  props: {
                    type: 'warning',
                    size: 'small'
                    },
                    style: {
                    margin: '0 4px 2px 0'
                    },
                    on: {
                      click: () => {
                          window.open(m_url,'_blank')
                      }
                    }
                  }, 'm预览')
              ])
            }
          },
      ],
      data: [],
      formData: {
        classify: '1',
        seltype: 'game_name',
        selval: '',
        phone: '',
      },
      isLoading: true,
      page: 1,
      per_page: 10,
      totalPages: 0,
      params: {
      },
      warnModal: false,
      modal_loading: false,
      telSelval: '',
      idnameSelval: ''
    }
    },
    mounted () {
      // console.log(getToken())
      this.getData();
    },
    methods: {
      getData() {
        const token = getToken(),
              classify = this.formData.classify || 1,
              seltype = this.formData.seltype || 0,
              selval = this.formData.selval || '',
              phone = this.formData.phone || '',
              page = this.page,
              per_page = this.per_page,
              params = {token, classify, seltype, selval, phone, page, per_page,}
              
        // console.log(params)
        api.feedbackList(params).then(res => {
            if(res.code === 0) {
              this.data = res.data.list
              this.totalPages = res.data.totlerows
              this.isLoading = false 
              // console.log(this.data)
              this.data.forEach(ele => {
                //格式化时间
                ele.addtime = this.$moment(ele.addtime*1000).format("YYYY-MM-DD k:mm");
                ele.listreason_name = ''
                if (ele.reason_name.length > 0) {
                    ele.reason_name.forEach(ele2 => {
                        ele.listreason_name += (ele2)
                    })
                }
              })
            } else {
              this.$Notice.error({title: res.msg})
            }
          })
        },
        changePage: function(page) {
          this.page = page;
          this.getData();
        },
        changePageNum: function(num) {
          this.per_page = num;
          this.getData();
        },
    }
})
</script>

<template>
    <div>
        <h3><Icon type="md-barcode" size=24 />反馈列表</h3>
        
        <div class="formBox">
            <Select v-model="formData.classify" style="width:100px">
                <Option value="1">游戏</Option>
                <Option value="2">应用</Option>
            </Select>
            <span> : </span>
            <Select v-model="formData.seltype" style="width:100px">
                <Option value="game_name">名称</Option>
                <Option value="game_id">ID</Option>
            </Select>

            <div class="idbox">
              <span></span><Input  v-model="formData.selval" clearable style="width: 200px" />
            </div>

            <div class="idbox">
              <span>手机号码/QQ号 : </span><Input  v-model="formData.phone" clearable style="width: 200px" />
            </div>

            <Button type="primary" style="width:100px" @click="getData()">
                <Icon type="ios-search" size=16></Icon>
                搜索
            </Button>
        </div>

        <Table 
            border 
            ref="selection" 
            :columns="columns" 
            :data="data" 
            :loading=isLoading
            >
        </Table>

        <div style="margin: 10px;overflow: hidden">
            <div style="float: right;">
                <Page :total="totalPages*1" show-total show-elevator show-sizer @on-page-size-change="changePageNum" @on-change="changePage" />
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    .formBox {
        height: 50px;
        margin-bottom: 20px;
        .searchBar{
            display: contents;
            padding-right: 20px;
        }
        .idbox {
            display:contents;
            span{
                margin-left:20px; 
            }
        }
        Button{
            margin-left: 20px;
            margin-right: 20px;
        }
        .ivu-modal{
            top: 200px !important;
        }
    }
</style>